<template>
	<div>
		<span><b class="comment-author">{{comment.userName}}:</b></span>
		<span class="comment">{{comment.content}}</span>
		<a class="comment-delete" @click.prevent="deleteMe">删除</a>
	</div>
</template>
<script>
	//用户名、内容组件
	export default {
		props: ['comment'],
		methods: {
			deleteMe() {
				//发射 一个id使用id进行删除
				/* alert(this.comment.id); */
				this.$emit('commentid', this.comment.id);
			}

		},
		created() {
			/* console.log(comment); */
		}

	}

//导出 一个js只能有一个默认default ， 如果要多个导出
//多导出 export { 名字, 名字 };
</script>
