<template>
	<div>
		<!-- @严重区分大小写，不能使用大写的字母 还有斜杠" - " 引号问题 -->
		<!-- @emitdata 是自己定义的接收数据的方式 对应this.$emit的发送数据 -->
		<cinput @emitdata="receivedata"></cinput>
		<clist :comments="comments" @deldate="delDate"></clist>
	</div>
</template>
<script>
	import commentList from './commentList.vue';
	import commentInput from './commentInput.vue';
	export default ({
		data() {
			return {
				comments: [],
			}
		},
		methods: {
			receivedata(res) {
				//接收数据添加到comments中
				this.comments.push(res);
				//把东西保存到本地缓存中,要先序列化
				localStorage.setItem('vm-comments', JSON.stringify(this.comments));
			},
			delDate(id) {
				this.comments = this.comments.filter((c) => c.id != id);
				localStorage.setItem('vm-comments', JSON.stringify(this.comments));
				//alert(id);
				//console.log(id + "comp");
			}
		},
		created() {
			// localStorage.clear();
			const comments = localStorage.getItem('vm-comments');
			if (comments) {
				//取出来要反序化
				this.comments = JSON.parse(comments);
				console.log(JSON.parse(comments));
			}
			console.log("sdsf");
		},
		//局部组件
		components: {
			cinput: commentInput,
			clist: commentList
		}
	});
</script>
